body { margin: 0; font-family: Arial, sans-serif; }






.sort-bar {
  display: flex;
  gap: 10px;
  margin-bottom: 16px;
}

.sort-button {
  padding: 6px 12px;
  background: #f0f0f0;
  border: 1px solid #bbb;
  border-radius: 6px;
  cursor: pointer;
}

.sort-button.active {
  background: black;
  color: white;
  border-color: black;
}















.clickable {
  cursor: pointer;
  transition: transform 0.2s ease;
}
.clickable:hover {
  transform: scale(1.02);
}

.back-link {
  display: inline-block;
  margin-bottom: 10px;
  color: #007bff;
  text-decoration: none;
}

.detail-title {
  font-size: 24px;
  margin-bottom: 16px;
}

.image-gallery {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  margin-bottom: 16px;
}
.gallery-img {
  width: 200px;
  height: 130px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.car-detail-content {
  background: white;
  padding: 16px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0,0,0,0.05);
}
.loading-text {
  padding: 16px;
  text-align: center;
}
.navbar {
  background: #f3f2f1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 24px;
  border-bottom: 1px solid #ddd;
}

.logo {
  font-size: 20px;
  font-weight: bold;
  color: #000;
  font-family: 'Arial Black', sans-serif;
}

.lang-switcher {
  background: #002C5F;
  color: #E0C292;
  border: 1px solid #E0C292;
  border-radius: 6px;
  font-weight: 600;
  padding: 4px 8px;
  cursor: pointer;
}
